<template>
	<view class="content">
		<!-- Â§¥ÂÉèÂíåÁôªÂΩï -->
		<view class="avatar-group">
			<view class="avatar-item" @click="handleAvatarClick">
				<up-avatar :src="avatar || defaultAvatar" shape="circle" size="55" />
				<text class="nickname">{{ name?.trim() ? name : 'ËØ∑ÁôªÂΩï' }}</text>
			</view>
			<view class="location">
				<up-icon name="map" color="#666" size="24" @click="onLocationClick" />
				<span class="location_text" @click="onLocationClick">{{place||"Ëé∑Âèñ‰ΩçÁΩÆ"}}</span>
			</view>
			<view class="Management_button">
				<up-button type="primary" shape="circle" text="ÁÆ°ÁêÜÂ≠¶Âëò" @click="Addto"></up-button>
			</view>
		</view>
		<!-- ÂäüËÉΩÂõæÊ†á -->
		<view class="function-icons">
			<view class="icon-item">
				<u-icon name="rmb-circle" size="40" color="#2ecc71" />
				<text>0.00</text>
			</view>
			<view class="icon-item">
				<up-icon name="fingerprint" size="40" color="#3498db" @click="OnJump" />
				<text>Á≠æÂà∞</text>
			</view>
			<view class="icon-item">
				<up-icon name="coupon" size="40" color="#e67e22" />
				<text>‰ºòÊÉ†Âà∏</text>
			</view>
			<view class="icon-item">
				<up-icon name="gift" size="40" color="#1abc9c" />
				<text>Á§ºÂìÅÂÖëÊç¢</text>
			</view>
		</view>

		<!-- ËÆ∞ÂΩïÂàóË°® -->
		<view class="record-list">
			<view class="record-item" @click="Achieve">
				<up-icon name="bookmark" size="24" color="#3498db" />
				<text>ÂÖëÊç¢ËÆ∞ÂΩï</text>
				<up-icon name="arrow-right" size="20" color="#ccc" />
			</view>
			<view class="record-item" @click="Achieve">
				<up-icon name="calendar" size="24" color="#e67e22" />
				<text>ÁßØÂàÜËÆ∞ÂΩï</text>
				<up-icon name="arrow-right" size="20" color="#ccc" />
			</view>
			<view class="record-item" @click="Achieve">
				<up-icon name="calendar" size="24" color="#1abc9c" />
				<text>ÊàêÁª©Âçï</text>
				<up-icon name="arrow-right" size="20" color="#ccc" />
			</view>
			<view class="record-item" @click="Achieve">
				<up-icon name="calendar" size="24" color="#9b59b6" />
				<text>ËÄÉÂã§ËÆ∞ÂΩï</text>
				<u-icon name="arrow-right" size="20" color="#ccc" />
			</view>
			<view class="record-item" @click="Achieve">
				<up-icon name="bell" size="24" color="#2ecc71" />
				<text>Êú∫ÊûÑÈÄöÁü•</text>
				<up-icon name="arrow-right" size="20" color="#ccc" />
			</view>
			<view class="record-item" @click="naviAchieve">
				<up-icon name="share" size="24" color="#e67e22" />
				<text>ÂàÜ‰∫´Â∞èÁ®ãÂ∫è</text>
				<up-icon name="arrow-right" size="20" color="#ccc" />
			</view>
		</view>

		<!-- ÁôªÂΩïÂºπÂá∫Ê°Ü -->
		<view class="popup">
			<u-popup v-model:show="showLoginPopup" mode="center" :mask-close="true" round="20" bgColor="#d8f3fa">
				<view class="popup-content">
					<button v-if="!avatar || !avatar.trim()" class="button-cover" open-type="chooseAvatar"
						@chooseavatar="ChooseAvatar">‰∏ä‰º†Â§¥ÂÉè</button>
					<image v-else :src="avatar" class="btn-avatar"></image>
					<input type="nickname" placeholder="ËØ∑ËæìÂÖ•ÊòµÁß∞" class="wechatname" @input="onInputNickname" />
					<input type="grade" placeholder="ËØ∑ËæìÂÖ•Âπ¥Á∫ß" class="popup-grade" @input="onInputGrade" />
					<!-- ‰ΩøÁî®ËäÇÊµÅÂêéÁöÑÁôªÂΩïÂáΩÊï∞ -->
					<u-button type="primary" shape="circle" @click="throttledWxLogin">ÁôªÂΩï</u-button>
				</view>
			</u-popup>
		</view>
	</view>
</template>

<script setup>
	import {
		ref,
		onMounted
	} from 'vue'
	import {
		onShow
	} from '@dcloudio/uni-app'

	import {
		MAP_KEY
	} from './Tencent MapsKey.json'
	const defaultAvatar = '/static/Avatar.png'
	const avatar = ref('')
	const name = ref('')
	const openid = ref('')
	const loggedIn = ref(false)
	const showLoginPopup = ref(false)
	let currentAvatarTarget = null;
	const grade = ref('');
	const show = ref(false);
	const mode = ref('single');

	const calendarRef = ref(null);
	//ÂÆö‰Ωç
	const place = ref('');
	// ÁÇπÂáªÂ§ñÈÉ®Â§¥ÂÉè+ ÊâìÂºÄÂºπÁ™ó
	const handleAvatarClick = () => {
		if (!name.value?.trim()) {
			showLoginPopup.value = true
			return
		}
		currentAvatarTarget = avatar
		ChooseAvatar()
	}
	const close = (promes) => {
		promes.value = false;
	}

	// ÈÄâÊã©Â§¥ÂÉè
	const ChooseAvatar = (e) => {
		avatar.value = e.detail.avatarUrl
	}

	// ÈÄâÊã©ÊòµÁß∞
	const onInputNickname = (e) => {
		name.value = e.detail.value
	}
	//Ëé∑ÂèñÂπ¥Á∫ß
	const onInputGrade = (e) => {
		grade.value = e.detail.value;
	}

	// Ë∑≥ËΩ¨ÁÆ°ÁêÜÂ≠¶Âëò
	const Addto = () => {
		uni.navigateTo({
			url: '/pages/manage/manage'
		})
	}

	// Âç†‰ΩçÁÇπÂáª‰∫ã‰ª∂
	// ÁÇπÂáªËé∑ÂèñÂÆö‰Ωç
	const onLocationClick = async () => {
		try {
			uni.showLoading({
				title: 'ÂÆö‰Ωç‰∏≠...',
				mask: true
			});

			// ÂÖàÊ£ÄÊü•ÊòØÂê¶ÊéàÊùÉÂÆö‰Ωç
			// #ifdef MP-WEIXIN
			const setting = await uni.getSetting();
			const hasLocationAuth = setting.authSetting['scope.userLocation'];

			// Êú™ÊéàÊùÉÂÆö‰Ωç ‚Üí ÂºïÂØºÂºÄÂêØ
			if (hasLocationAuth === false) {
				uni.hideLoading()
				return uni.showModal({
					title: 'ÊèêÁ§∫',
					content: 'Ê£ÄÊµãÂà∞ÊÇ®Êú™ÂºÄÂêØÂÆö‰ΩçÊùÉÈôêÔºåËØ∑ÂâçÂæÄËÆæÁΩÆÂºÄÂêØ„ÄÇ',
					confirmText: 'ÂéªËÆæÁΩÆ',
					showCancel: false,
					success(res) {
						if (res.confirm) uni.openSetting();
					}
				});
			}
			// #endif

			// ‚òÖ Áõ¥Êé•‰ΩøÁî®Á≤æÁ°ÆÂÆö‰ΩçÔºàisHighAccuracy: trueÔºâ
			const locationRes = await uni.getLocation({
				type: 'gcj02',
				geocode: true,
				isHighAccuracy: true, // Á≤æÁ°ÆÂÆö‰Ωç
				highAccuracyExpireTime: 3000 // Ë∂ÖÊó∂Êó∂Èó¥
			});
			// console.log("üìç ÂéüÂßãÂÆö‰Ωç‰ø°ÊÅØÔºàuni.getLocationÔºâ:", locationRes);
			const {
				latitude,
				longitude
			} = locationRes;

			// ÂèçÂêëËß£Êûê
			const key = MAP_KEY;
			const url = `https://apis.map.qq.com/ws/geocoder/v1/?location=${latitude},${longitude}&key=${key}`;
			const res = await uni.request({
				url
			});
			console.log("üìç ËÖæËÆØÂú∞ÂõæËß£ÊûêÁªìÊûú:", res.data);
			if (res.data.status === 0) {
				const info = res.data.result;
				const standardAddress = info.formatted_addresses?.standard_address;
				place.value = standardAddress;

				uni.showToast({
					title: 'Ëé∑Âèñ‰ΩçÁΩÆÊàêÂäü',
					icon: 'success'
				});
			} else {
				throw new Error(res.data.message);
			}

		} catch (err) {
			uni.hideLoading()
			console.error('ÂÆö‰ΩçÂ§±Ë¥•:', err);

			// Áî®Êà∑ÊãíÁªùÊéàÊùÉ
			if (err.errMsg?.includes('auth')) {
				return uni.showModal({
					title: 'ÊèêÁ§∫',
					content: 'ÂÆö‰ΩçÊùÉÈôêÊú™ÂºÄÂêØÔºåËØ∑ÂâçÂæÄËÆæÁΩÆÊâìÂºÄÂÆö‰ΩçÊùÉÈôêÂêéÈáçËØï„ÄÇ',
					confirmText: 'ÂéªËÆæÁΩÆ',
					showCancel: false,
					success(res) {
						if (res.confirm) uni.openSetting();
					}
				});
			}

			// ‚òÖ Á≥ªÁªüÂÖ≥Èó≠‰∫Ü GPSÔºåÊØîÂ¶ÇÂÆâÂçìÂÖ≥Èó≠ÂÆö‰ΩçÊúçÂä°
			if (err.errMsg?.includes('system permission') || err.errMsg?.includes('system')) {
				uni.hideLoading()
				return uni.showModal({
					title: 'ÊèêÁ§∫',
					content: 'ÊâãÊú∫ÂÆö‰ΩçÊúçÂä°Êú™ÂºÄÂêØÔºåËØ∑ÂâçÂæÄÁ≥ªÁªüËÆæÁΩÆÊâìÂºÄ GPS ÂêéÈáçËØï„ÄÇ',
					showCancel: false
				});
			}

			uni.showToast({
				title: 'ÂÆö‰ΩçÂ§±Ë¥•ÔºåËØ∑ÈáçËØï',
				icon: 'none'
			});
		} finally {
			uni.hideLoading();
		}
	};


	const Achieve = () => {
		uni.showToast({
			title: 'ÂäüËÉΩÂæÖÂÆûÁé∞',
			icon: 'none'
		})
	}

	// ËäÇÊµÅÂáΩÊï∞
	function throttle(fn, delay = 2000) {
		let lastTime = 0
		return function(...args) {
			const now = Date.now()
			if (now - lastTime >= delay) {
				lastTime = now
				fn.apply(this, args)
			}
		}
	}
	const validate = (fields) => {
		for (const {
				value,
				msg
			}
			of fields) {
			if (!value?.trim()) {
				uni.showToast({
					title: msg,
					icon: 'none'
				});
				return false
			}
		}
		return true
	}

	// ÂæÆ‰ø°ÁôªÂΩïÔºàÂè™Êãø openid/session_keyÔºâ
	async function wxLogin() {
		/**
		 @grade Á±ªÂûãÈóÆÈ¢òÊú™ÂÅöÁ±ªÂûãÊ†°È™åËØ•‰∏∫Âπ¥Á∫ßÈÄâÊã©*/
		try {
			const valid = validate([{
					value: avatar.value,
					msg: 'ËØ∑‰∏ä‰º†Â§¥ÂÉè'
				},
				{
					value: name.value,
					msg: 'ËØ∑ËæìÂÖ•ÊòµÁß∞'
				},
				{
					value: grade.value,
					msg: 'ËØ∑ËæìÂÖ•Âπ¥Á∫ß'
				}
			]);
			if (!valid) return

			const loginRes = await uni.login()
			const payload = {
				code: loginRes.code,
				avatar: avatar.value,
				nickname: name.value,
				grade: grade.value,
			}
			const res = await uniCloud.callFunction({
				name: 'weixinLogin',
				data: payload
			})
			if (res.result.code === 0) {
				openid.value = res.result.data.openid
				loggedIn.value = true;
				showLoginPopup.value = false // üîπÂÖ≥Èó≠ÂºπÁ™ó
				uni.showToast({
					title: 'ÁôªÂΩïÊàêÂäü',
					icon: 'success'
				})

			} else {
				uni.showToast({
					title: res.result.msg || 'ÁôªÂΩïÂ§±Ë¥•',
					icon: 'none'
				})
			}
		} catch (err) {
			console.error('ÁôªÂΩïÂ§±Ë¥•:', err);
			uni.showToast({
				title: 'ÁôªÂΩïÂ§±Ë¥•ÔºåËØ∑ÈáçËØï',
				icon: 'none'
			});
		}
	}

	// Áî®ËäÇÊµÅÂáΩÊï∞ÂåÖË£ÖÁôªÂΩï
	const throttledWxLogin = throttle(wxLogin, 2000)
	// È°µÈù¢ÊòæÁ§∫Êó∂Êõ¥Êñ∞ tabBar
	// È°µÈù¢ÊòæÁ§∫Êó∂Êõ¥Êñ∞ tabBar Âπ∂Ê£ÄÊµãÁôªÂΩï
	onShow(() => {
		const page = getCurrentPages().pop()
		const tabBar = page?.getTabBar?.()
		if (typeof tabBar?.setData === 'function') {
			tabBar.setData({
				selected: 1
			})
		}

		// Ê£ÄÊµãÊòØÂê¶ÁôªÂΩï
		if (!loggedIn.value) {
			uni.showToast({
				title: 'ËØ∑ÂÖàÁôªÂΩï',
				icon: 'none'
			})
			showLoginPopup.value = true;
		}
	});
	const onCalendarClose = () => {
		show.value = false;
	};
	/*Ë∑≥ËΩ¨Á≠æÂà∞È°µÈù¢*/
	const OnJump = () => {
		if (!loggedIn.value) {
			uni.navigateTo({
				url: '/pages/Sign/index'
			});
		}

	};
</script>

<style lang="scss" scoped>
	@import './index.scss';
</style>